___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --blue : #35CB8B;
    --lt-blue : #F9FFFF;
    --bg : #E92121;
}

body {
    background: #FEEBDD;
}

header {
    height: 50px;
}

nav {
    display: grid;
    height: 100%;
    grid-template-columns: 1fr 4fr;
    gap: 5px;


    >div {
        border:1px solid #35CB8B;
        border-radius: 14px;
    }

    >div:last-child {
        border-radius: 14px 0 0 14px;
    }
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #35CB8B;
    text-transform: capitalize;
    font-weight: 800;
    font-size: larger;
    border:none;

    >div:first-child {
        flex:1;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            height:auto;
            width: 40px;
        }
    }
    >div:last-child {
        flex:3;
    }
}

.projects {
    display: grid;
    grid-template-columns: repeat(12,1fr);
    padding: 5px;
    gap: 5px;
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
    overflow: auto;
    background:#FEFFFF;

    div {
        background-color: var(--lt-blue);
        border-radius: 7px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid var(--blue);
        color: rgba(0, 0, 0,.7);
        text-transform: capitalize;
    }

    >div:hover {
        background-color: rgba(53, 203, 139,.4);
        border: 1px solid black;
        color:black;
    }

    >div:last-child {
        background-color: #35CB8B;
        color: black;
    }

    >div:last-child:hover {
        background-color: white;
        color: black;
        border: 1px solid black;
    }
}

.new-list {
    position: fixed;
    bottom: 50px;
    right: 50px;
    font-size: 2rem;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    color: white;
    border-radius: 30px;
    background-color: #35CB8B;
}

.new-list:hover {

    border: 10px solid var(--lt-blue);
    border-radius: 40px;
} 

#lists {
    border: 2px solid rgba(0, 0, 0,.6);
    border-radius: 12px;
    height: 780px;
    padding-block:  20px;
    padding-inline: 30px;
    margin-top: 30px;
    overflow: auto;
    gap: 10px;

    display: grid;
    grid-template-rows: repeat(12, 50px);

    >div {
        background-color: #FFF6ED;
        border: 2px solid rgba(0, 0, 0, .2);
        border-radius: 10px;
    }
}

.list {
    height: 100%;
    display: flex;
    gap: 5px;

    >div {
        color: #3B3246;
        display: flex;
        flex : 1;
        > div:first-child {
            flex: 1;
            display: flex;
            align-items: center;
    
            >input {
                flex: 1;
                height: 40%;
                width: 40%;
            }
        }

    
        >.list-div {
            flex: 8;
            >div {
                flex: 1;
                height: 100%;
                display: flex;
                justify-content: space-between;
                padding-inline: 10px;
             
                >p:first-child {
                    font-weight: 900;
                }

                >p:last-child {
                    font-weight: 500;
                    color:coral;
                }
            }
        }
    }

}

.new-task {
    display: flex;
    border: 2px solid black;
    border-radius: 10px;
    width: 90%;
    height: 300px;
    position: fixed;
    bottom: 40%;
    left: 5%;
    background-color:#F9FFFF;

    [type="text"], textarea {
        background-color: white;
    }

    [type="text"]:not(:focus), textarea:not(:focus) {
        background-color: rgba(53, 203, 139,.1);
    }

    textarea {
        border-radius: 5px;
    }


    form {
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: 20px;

        [type="text"], textarea {
            font-size: large;
            color: black;
            padding:5px;
        }


        >div {
            flex:1;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
        } 

        .task-input-head {
            gap: 5px;
            >input {
                height: 40px;
                width: 100%;
                border-radius: 5px;
            }
            >textarea {
                width: 100%;
                height: 100px;
                max-height: 100px;
                min-height: 40px;
                resize: vertical;
            }
        }

        .input-date-category {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
        }

        .add-task {
            background-color: var(--blue);
            max-height: 50px;
            width: 50px;
            color:white;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 25px;
            font-size: 2rem;
            align-self: flex-end;
        }

        .add-task:hover {
            border: 2px solid white;
        }

        select, option {
            text-transform: capitalize;
        }
    }
}

.full-list {
    position: relative;
    height: 250px;
    width: 90vw;
    left: 3vw;
    border-radius: 5px;
    position: fixed;
    bottom: 30%;
    display: grid;
    grid-template-rows: 1fr 3fr 1fr;
    background-color: #FFF6ED;
    border: 2px solid black;
    gap: 5px;
    padding: 10px;
    color: #3B3246;

    .full-extras {
        display: flex;
        background-color:#35CB8B;
        border-radius: 5px;
        >div {
            display: flex;
            flex: 1;

            >div:first-child {
                display: flex;
                align-items: center;
                flex:1;
                padding-inline: 5px;
            }
            >div:last-child {
                flex: 9;
            }
        }
    }

    .full-titles, .full-des {
        display: flex;
        >div:first-child {
            display: flex;
            align-items:center;
            justify-content:start;
            flex: 1;
        }
        >div:last-child {
            flex: 9;
            background-color:#35CB8B;
            border: 2px double white;
        }

        background-color: #FFEDDF;

    }
}

.cancel-btn {
    position: absolute;
    font-weight: 800;
    top: 5px;
    right: 10px;
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #3B3246;
    color: white;
    border-radius: 3px;
}

.cancel-btn:hover {
    background: #E92121;
    color: white;
}

[class*='content'] {
    display: flex;
    align-items: center;
    border-radius: 5px;
    padding: 10px;
    color: #3B3246;
    font-weight: 700;
}

/*  */

.extra-card {
    position: fixed;
    display: flex;
    flex-direction: column;
    height: 100px;
    width: 20px;
    font-size: 0rem;
    background: rgba(53, 203, 139,.7);
    border: 2px solid white;
    border-radius: 4px;
    padding: 5px;
    left: 0;
    bottom: 50vh;
    >div {
        flex: 1;
        display: flex;
        gap: 5px;

        >div:first-child {
            height: 10px;
            width: 10px;
            border: 1px solid whitesmoke;
            border-radius: 5px;
        }
    }

    >div:first-child {
        display: flex;
        justify-content: center;
    }

    >div:last-child {
        height: 20px;
        border: 1px solid white;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        border-radius: 3px;
        >img {
            height: auto;
        }
    }

    >div:last-child:hover {
        background-color: #FFEDDF;
    }
}

/*  */

.extra-card:hover {
    width: 50px;
    font-size: .8rem;
    background: #35CB8B;
}

.low-card {
    >div:first-child {
        background-color: #F89F0D;
    }
}

.mid-card {
    >div:first-child {
        background-color: green;
    }
}

.high-card {
    >div:first-child {
        background-color: tomato;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,8BAA8B;IAC9B,QAAQ;;;IAGR;QACI,wBAAwB;QACxB,mBAAmB;IACvB;;IAEA;QACI,4BAA4B;IAChC;AACJ;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,cAAc;IACd,0BAA0B;IAC1B,gBAAgB;IAChB,iBAAiB;IACjB,WAAW;;IAEX;QACI,MAAM;QACN,aAAa;QACb,uBAAuB;QACvB,mBAAmB;;QAEnB;YACI,WAAW;YACX,WAAW;QACf;IACJ;IACA;QACI,MAAM;IACV;AACJ;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,YAAY;IACZ,QAAQ;IACR,sBAAsB;IACtB,sBAAsB;IACtB,cAAc;IACd,kBAAkB;;IAElB;QACI,gCAAgC;QAChC,kBAAkB;QAClB,aAAa;QACb,uBAAuB;QACvB,mBAAmB;QACnB,6BAA6B;QAC7B,uBAAuB;QACvB,0BAA0B;IAC9B;;IAEA;QACI,uCAAuC;QACvC,uBAAuB;QACvB,WAAW;IACf;;IAEA;QACI,yBAAyB;QACzB,YAAY;IAChB;;IAEA;QACI,uBAAuB;QACvB,YAAY;QACZ,uBAAuB;IAC3B;AACJ;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,WAAW;IACX,eAAe;IACf,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;;IAEI,iCAAiC;IACjC,mBAAmB;AACvB;;AAEA;IACI,kCAAkC;IAClC,mBAAmB;IACnB,aAAa;IACb,oBAAoB;IACpB,oBAAoB;IACpB,gBAAgB;IAChB,cAAc;IACd,SAAS;;IAET,aAAa;IACb,oCAAoC;;IAEpC;QACI,yBAAyB;QACzB,mCAAmC;QACnC,mBAAmB;IACvB;AACJ;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,QAAQ;;IAER;QACI,cAAc;QACd,aAAa;QACb,QAAQ;QACR;YACI,OAAO;YACP,aAAa;YACb,mBAAmB;;YAEnB;gBACI,OAAO;gBACP,WAAW;gBACX,UAAU;YACd;QACJ;;;QAGA;YACI,OAAO;YACP;gBACI,OAAO;gBACP,YAAY;gBACZ,aAAa;gBACb,8BAA8B;gBAC9B,oBAAoB;;gBAEpB;oBACI,gBAAgB;gBACpB;;gBAEA;oBACI,gBAAgB;oBAChB,WAAW;gBACf;YACJ;QACJ;IACJ;;AAEJ;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,UAAU;IACV,aAAa;IACb,eAAe;IACf,WAAW;IACX,QAAQ;IACR,wBAAwB;;IAExB;QACI,uBAAuB;IAC3B;;IAEA;QACI,uCAAuC;IAC3C;;IAEA;QACI,kBAAkB;IACtB;;;IAGA;QACI,OAAO;QACP,aAAa;QACb,sBAAsB;QACtB,aAAa;;QAEb;YACI,gBAAgB;YAChB,YAAY;YACZ,WAAW;QACf;;;QAGA;YACI,MAAM;YACN,aAAa;YACb,sBAAsB;YACtB,6BAA6B;QACjC;;QAEA;YACI,QAAQ;YACR;gBACI,YAAY;gBACZ,WAAW;gBACX,kBAAkB;YACtB;YACA;gBACI,WAAW;gBACX,aAAa;gBACb,iBAAiB;gBACjB,gBAAgB;gBAChB,gBAAgB;Y